body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

body {
  font-family: monospace, system-ui, -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  background-color: #6763E7;
  min-width: 350px;
}


.login,
.signup {

  margin-top: 5% !important;
  max-width: 500px;
  margin: auto;
  padding: 20px;
  background-color: rgb(255, 253, 253);

}

.login h3,
.signup h3 {
  word-spacing: 5px;
  font-weight: 700;
  color: rgb(5, 24, 131) !important;
}

.formbtn {

  border: none;
  color: #ffff;
  padding: 10px 0;
  font-size: 20px;
  font-weight: 700;
  border-radius: 10px;
  width: 100% !important;
  background-color: rgb(5, 24, 131) !important;

}

/* .formbtn:hover {
  transition: 0.5s;
  border-bottom: 5px solid rgb(155, 87, 87);
} */

@media screen and (max-width: 768px) {

  .login,
  .signup {
      padding: 20px;
      margin-top: 15% !important;
  }
}